import { getProgressDisplayStr } from '@jbrowse/core/util';
function generate_record(qname, qstart, qend, qstrand, tname, tstart, tend, cigar, numMatches) {
    return {
        qname,
        qstart,
        qend,
        tname,
        tstart,
        tend,
        strand: qstrand === '-' ? -1 : 1,
        extra: {
            numMatches,
            blockLen: Math.max(qend - qstart, tend - tstart),
            mappingQual: 0,
            cg: cigar,
        },
    };
}
export function paf_chain2paf(buffer, opts) {
    const { statusCallback = () => { } } = opts || {};
    let t_name = '';
    let t_start = 0;
    let t_end = 0;
    let q_name = '';
    let q_size = '';
    let q_strand = '';
    let q_start = 0;
    let q_end = 0;
    let num_matches = 0;
    let cigar = '';
    const records = [];
    let blockStart = 0;
    const decoder = new TextDecoder('utf8');
    let s = performance.now();
    let i = 0;
    while (blockStart < buffer.length) {
        if (i++ % 10000 === 0 && performance.now() - s > 50) {
            statusCallback(`Loading ${getProgressDisplayStr(blockStart, buffer.length)}`);
            s = performance.now();
        }
        const n = buffer.indexOf(10, blockStart);
        if (n === -1) {
            break;
        }
        const b = buffer.subarray(blockStart, n);
        const line = decoder.decode(b).trim();
        blockStart = n + 1;
        if (!line || line.startsWith('#')) {
            continue;
        }
        const l_vec = line.split(/[ \t]+/);
        if (l_vec[0] === 'chain') {
            if (cigar) {
                records.push(generate_record(q_name, q_start, q_end, q_strand, t_name, t_start, t_end, cigar, num_matches));
            }
            t_name = l_vec[2];
            t_start = +l_vec[5];
            t_end = +l_vec[6];
            q_name = l_vec[7];
            q_size = l_vec[8];
            q_strand = l_vec[9];
            q_start = +l_vec[10];
            q_end = +l_vec[11];
            if (q_strand === '-') {
                const tmp = q_start;
                q_start = +q_size - q_end;
                q_end = +q_size - tmp;
            }
            num_matches = 0;
            cigar = '';
        }
        else {
            const size_ungapped_alignment = +l_vec[0] || 0;
            const diff_in_target = l_vec.length > 1 ? +l_vec[1] : 0;
            const diff_in_query = l_vec.length > 2 ? +l_vec[2] : 0;
            if (size_ungapped_alignment !== 0) {
                num_matches += +size_ungapped_alignment;
                cigar += `${size_ungapped_alignment}M`;
            }
            if (diff_in_query !== 0) {
                cigar += `${diff_in_query}I`;
            }
            if (diff_in_target !== 0) {
                cigar += `${diff_in_target}D`;
            }
        }
    }
    if (cigar) {
        generate_record(q_name, q_start, q_end, q_strand, t_name, t_start, t_end, cigar, num_matches);
    }
    statusCallback('');
    return records;
}
